section.general-careers {
  padding: 3.813rem 1rem 5rem;
}

#page-title.general-careers {
  padding-top: 5.813rem;

  ul {
    padding-left: 0px;

    li {
      a {
        color: $black-op-8;
        text-decoration: underline;
        font-weight: 500;
        line-height: normal;
        font-size: $font-size-13;
      }

      &:before { border: 0px; }
    }
  }

  .career {
    list-style: none;
  }
}

.careers-header-container {
  margin-top: 0.56rem;

  h5 {
    font-weight: 500;
    margin-bottom: 1.438rem;
  }  

  h2 {
    line-height: 1.21;
    margin-bottom: 0em;
  }
}

#page .c-desc {
  margin-top: 1.438rem;

  p:first-child {
    margin-top: 0em;
  }

  p {
    margin: 1.875rem 0rem;
  }

  h5 {
   margin-top: 0em;
   font-weight: 500;
   text-transform: uppercase;
   min-height: 48px;
  }

  li {
    font-size: $font-size-13;
    line-height: 1.77;
    color: $general-op-6;
    margin-bottom: 0.563rem;
  }

} 

#c02 {
  h5 {
    font-weight: 500;
  }
}