section.general-about {
  width: 1180px;
  margin: auto
}

section.general-about {
  padding: 2rem 1rem;
}

.about-us-header {
  margin-top: 3.563rem;
}

.about-us-desc {
  margin-bottom: 1.5rem;
}

#statistic {
  background-color: $section-bg1;

  h2 {
    margin-top: 1.625rem;
    margin-bottom: 2.563rem;
    line-height: 1.21;
  }

  h1 { margin-top: 0rem; }

  .number-header { margin-bottom: 1.125rem; }

  .core-desc { 
    text-transform: uppercase;
    color: $general-op-7;
    font-weight: 500;
    line-height: normal;
    font-size: $font-size-12;
    width: 209px;
    margin: auto;
  }
}

#particl_foundation {
  padding: 0rem;

  .particl-foundation-bg {
    background-image: url('/assets/img/illustrations/about_us_banner.png');
    background-position: center center;
    background-size: cover;
    height: 411px;
    width: 100%;
    background-repeat: no-repeat;
    position: relative;
    margin: auto;

    h1 {
      position: absolute;
      bottom: 55px;
      left: 134px;
      margin: auto;
      width: 350px;
      height: 60px;
      background-color: $white;
      text-align: center;
    }
  }
}

#page section#about_us {
  background-color: $section-bg1;

  .about-us-item {
    margin-top: 0.498rem;
    margin-bottom: 2.188rem;
  }

  h5 {
    text-transform: uppercase;
    font-weigth: 500;
  }

  p {
    font-size: $font-size-13;
    line-height: 1.77;
  }
}

.team-header {
  margin-top: 0.813rem;

  h1 {
    margin-bottom: 1.5rem;
  }
}

.team-desc {
  margin-bottom: 1.188rem;
}

#team {
  background-color: $section-bg1;
  margin-top: 2.5rem;
}

.locate {
  padding-top: 3.125rem;
  margin-bottom: 3.688rem;
}

.btn-explore {
  line-height: 2;
  font-weight: 600;
  font-size: $font-size-13;
  padding: 0.563rem 1.825rem;
}

.locate-desc p {
  margin-bottom: 4rem;
}