footer, .mobile-header-footer {
  padding: 2.625rem 0rem 0rem;
  text-align: left;
  border-top: 2px solid #fff;

  h4 {
    font-size: 0.813rem;
  }

  .part {
    .title {
      margin: 0.5rem 0rem 0rem;
    }

    ul {
      list-style: none;
      padding: 0px;
    }
  }

  .links-container {
    margin-bottom: 1rem;

    ul {
      margin-top: 25px;
      margin-bottom: 15px;

      li {
        line-height: 2.09;
        padding-bottom: 5px;

        a {
          color: $general-op-6;
          font-size: 0.688rem;
        }

        &.copyright {
          color: $general-op-6;
          font-size: 0.688rem; 
        }
      }
    }
  }

  .inner-line-footer {
    border-top: 1px solid $white;
  }

  .social-icons-container {
    padding: 1.313rem 0rem;

    img {
      max-width: 6.313rem;
    }

    a {
      display: inline-block;
      width: 47px;
      height: 47px;

      position: relative;
    }
  }

  .footer-btn:before {
    content: '';
    position: absolute;
    background-size: contain;
    background-repeat: no-repeat;
    bottom: 0px;
    top: 0px;
    left: 0px;
    margin: auto;
    width: 46px;
    height: 46px;
  }

  .email-btn:before {
    background-image: url("/assets/img/ico/new/email_icon.png");
  }

  .twitter-btn:before {
    background-image: url("/assets/img/ico/new/twitter_icon.png");
  }

  .main-btn:before {
    background-image: url("/assets/img/ico/new/main_icon.png");
  }

  .github-btn:before {
    background-image: url("/assets/img/ico/new/github_icon.png");
  }

  .bitcoin-btn:before {
    background-image: url("/assets/img/ico/new/bitcoin_icon.png");
  }

  .youtube-btn:before {
    background-image: url("/assets/img/ico/new/youtube_icon.png");
  }

  .sharp-btn:before {
    background-image: url("/assets/img/ico/new/sharp_icon.png");
  }

  .riot-btn:before {
    background-image: url("/assets/img/ico/new/r_icon.png");
  }

  .telegram-btn:before {
    background-image: url("/assets/img/ico/new/telegram_icon.png");
  }

  .discord-btn:before {
    background-image: url("/assets/img/ico/new/d_icon.png");
  }
}