nav {
  &.column {
    @include before_md { 
      background-color: $header-background;
      position: fixed;
      top: 3.375rem;
      z-index: 1;
    }
  }
  
  &.column {
    @include md { 
      position: relative;
    }
  }

  @include break(l) {
    display: block !important;
    width: auto;
    position: static;
  }

  ul {
    @extend %clearfix;
    list-style: none;
    margin: 0rem;
    padding: 0rem;

    li {
      float: left;
      display: block;
      width: 50%;

      @include before_md {
        width: 100%;
      }

      a { // menu items
        display: block;
        color: $black;
        font-size: 0.81rem;
        font-family: 'GraphikLCGSemiBold';
        line-height: 26px;
        text-transform: none;
        border-bottom: 1px solid $black;
        border-right: 1px solid $black;
        padding: 0.875rem 0.73rem;

        @include before_md { color: $white; }

        &:hover, &:focus {
          color: $nav_hover;
          background: $nav_hover_bg;
        }

        &.home-download-btn {
          color: $btn-text-color;
        }
      }

      &:nth-of-type(2n+2) > a {
        border-right-color: transparent;
        @include break(s) { border-right: 1px solid $black; }
      }

      @include break(s) {
        width: 33.333%;
        &:nth-of-type(3n+3) > a { border-right-color: transparent; }
      }

      @include break(m) {
        width: 25%;
        &:nth-of-type(3n+3) > a { border-right: 1px solid $black; }
        &:nth-of-type(4n+4) > a { border-right-color: transparent; }
      }

      @include break(l) { // desktop menu
        width: auto;
        a { border: none !important; }
      }
    }

  }
}

.mobile-header-footer {
  display: none;

  .title {
    color: $white;
  }

  ul {
    li{}

    a {
      color: $white-op-6;
      border: 0px;
    }    
  }

  @include before_md {
    display: block;
  }
}

body.menu-opened {
  @include before_md {
    overflow: hidden;
  }

  nav {
    @include before_md {
      overflow-y: scroll;
      height: 100%;
    }
  }
}