.header {
  background-color: $header-bg;
  height: 3.563rem;
  width: 100%;
  z-index: 2;
  position: fixed;
  top: 0rem;

  @include before_md {
    background-color: $header-mobile-bg;
    height: 3.375rem;
  }

  button {
    &:focus, &:hover {
      background: transparent;
      outline: 0px;
    }
  }

  #menu-toggle { // mobile: toggle menu button
    float: right;
    border: 0px;
    color: $white-op-8;
    margin: 0.967rem 0rem;

    @include break(l) { display: none; }

    i.ico { margin: 0rem; }
  }

  .header-chart-container {
    color: $header-chart-text;
    font-weight: 500;
    font-size: 0.625rem;
    text-transform: uppercase;
    text-align: center;
    height: 3.563rem;

    & > div {
      border: 0px !important;

      & > div:first-child {

        & > div:first-child {

          width: 100% !important;
          text-align: center !important;
          float: none !important;
          line-height: normal !important;
          padding: 0px !important;

          & > span:first-child > a {
            color: transparent !important;

            &:before {
              content:'1 part =';
              color: $header-chart-text;
              font-weight: 500;
              font-size: 0.625rem;
              text-transform: uppercase;
              text-align: center;
            }
          }

          & > span:last-child {
            color: $header-chart-text;
            font-weight: 500;
            font-size: 0.625rem !important;
            text-transform: uppercase;
            text-align: center;
          }
        }

        & > div:last-child {
         display: none;
        }
      }

      & > div:last-child {
        display: none;
      }

    }             

    @include before_md {
      height: 3.375rem;
      color: $white;
      text-align: left;
    }

    span {
      padding-top: 1.25rem;
      display: inline-block;
      vertical-align: middle;
      height: 100%;
      padding-right: 4px;

      @include before_md {
        padding-right: 3px;
      }      
    }

    .header-chart-percent {
      @include before_md {
        display: none;
      }
    }
  }
}

header {
  height: 3.563rem;

  @include before_md {
    height: 3.375rem;
  }
  
  .logo {
    padding-top: 0.656rem;
    @extend .tfx;
    
    @include before_md {
      padding-top: 0.563rem;
    }

    .desktop-logo img { 
      @include before_md { display: none; } 
      @include md { display: inline-block; } 
    }

    .mobile-logo img { 
      @include before_md { display: inline-block; } 
      @include md { display: none; } 
    }

    img {
      @extend .tfx;
      max-width: 5.563rem;
      height: 36px;
    }
  }
}