.owl-theme {
  .owl-dots {

    .owl-dot {
      margin: 0px 10px;
      font-size: 0.75rem;
      color: $general-text-color;
      font-weight: 500;
    }

    .owl-dot.active {
      border: 1.5px solid $carousel-dot-color;
      border-radius: 50%;
      width: 20px;
      height: 20px;
    }
  }
}

.owl-carousel.owl-drag .owl-item {
  @include box-shadow(0 4px 25px 0 #f0f0f0);
  background-color: $white;
  @include border-radius(10px);
  max-width: 22.313rem;
  margin-top: 0.41rem;
  margin-bottom: 2.375rem;

  .img-container {
    padding: 2.5rem 3rem 2.5rem 3rem;
    max-width: 357px;
    max-height: 300px;
    min-height: 300px;
    border-bottom: 1px solid $carousel-border-color;
  }
 
  img {
    max-width: 357px;
    height: auto;
  }

  .content-container {
    padding: 0.81rem 3.44rem 0rem 2.56rem;

    h5 {
      font-size: 0.75rem;
      color: $h5-subheader;
      font-weight: 500;
      text-transform: uppercase;
    }

    h2 {
      width: 281px;
      min-height: 68px;
      height: 100%;
      margin: 0rem;
      line-height: 1.21;
      color: $h2-header-color;
    }

    p {
      width: 295px;
      line-height: 1.77;
      color: $text-color;
      font-size: 0.813rem;
      min-height: 161px;
      margin-bottom: 2.375rem;
    }
  }
}