section.general-download {
  padding: 3.875rem 1rem;
}

.wallets-header {
  h1 {
    margin-bottom: 0.875rem;
  }
}

.desc-form {
  width: 437px;
}

.wallets-view {
  margin-top: 2.125rem;

  table {
    border-color: $hr-color;
  }

  .download-table {
    margin-bottom: 2.063rem;

    th {
      font-size: $font-size-19;
      line-height: 1.42;
      padding-bottom: 0.625rem;
    }

    td {
      padding: 0.563rem 0rem;
      position: relative;

      
      &.icon:before {
        content: '';
        position: absolute;
        background-size: contain;
        background-repeat: no-repeat;
        bottom: 0px;
        top: 0px;
        left: 0px;
        margin: auto;
        width: 23px;
        height: 23px;
      }

      &.icon-check:before {
        background-image: url("/assets/img/ico/new/downloads_check.png");
      }

      &.icon-uncheck:before {
        background-image: url("/assets/img/ico/new/downloads_uncheck.png");
      }

      &.type-name, &.platform-name {
        color: $general-op-6;
        line-height: 1.87;
        font-size: $font-size-15;
      }
    }

    tr:last-child {
      border: 0px;
    }
  }
}

section.version-list {
  padding: 2.75rem 1rem 1.625rem 1rem;

  .copay-img-cont {
    text-align: center;

    img {
      width: 117px;
    }
  }

  h5 {
    margin-top: 0px;
    margin-bottom: 1.063rem;
    font-weight: 500;
    line-height: normal;
  }

  h2 {
    margin-bottom: 0.75rem;
    line-height: 1.21;
  }

  img {
    margin-top: 2.25rem;
  }

  p {
    width: 379px;
  }

  .btn-installation {
    color: $black-op-8;
    font-size: $font-size-13;
    font-weight: 500;
    line-height: normal;
    text-decoration: underline;
    margin-bottom: 1.438rem;
    display: inline-block;
  }
}

.last-hr-copay {
  margin-bottom: 1.438rem;
}

.version-list-links {
  margin-bottom: 0.75rem;

  .link {
    padding: 5px 65px 20px 30px;
    position: relative;
    color: $general-text-color;
    font-size: $font-size-12;
    font-weight: 600;
    display: inline-block;
  }

   .link:before {
      content:'';
      position: absolute;
      background-size: contain;
      background-repeat: no-repeat;
      bottom: 0px;
      top: 4px;
      left: 0px;
      width: 20px;
      height: 20px;
      opacity: 0.7;
    }

    .win-64:before, .win-32:before {
      background-image: url("/assets/img/ico/new/windows-8.png");
    }

    .linux-64:before, .linux-32:before {
      background-image: url("/assets/img/ico/new/linux.png");
    }

    .mac-64:before {
      background-image: url("/assets/img/ico/new/mac-os-icon.png");
    }

    .source-code:before {
      background-image: url("/assets/img/ico/new/github-logo.png");
    }

    .freebsd:before {
      background-image: url("/assets/img/ico/new/free-bsd.png");
    }

}